.backgrounddd {
  position: relative;
  z-index: 1;
}

.backgrounddd:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scaleY(0);
  transform-origin: bottom center;
  background: #078dd7;
  /* background: #121a5e; */
  z-index: -1;
  transition: transform 0.3s;
}

.backgrounddd:hover::after {
  transform: scaleY(1);
}
.backgrounddd:hover {
  color: white;
}

.carousel {
  width: 100%;
  overflow: hidden;
  position: relative;
}

.carouselItems {
  display: flex;
  animation: slide 13s infinite;
}

.carouselItem {
  flex: 0 0 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes slide {
  0% {
    transform: translateX(0);
  }
  23.08% {
    /* 3 seconds transition out of 13 seconds (100% / 4 items) */
    transform: translateX(0);
  }
  30.77% {
    /* 3 seconds pause (each item shown for 4 seconds) */
    transform: translateX(-100%);
  }
  53.85% {
    /* 3 seconds transition out of 13 seconds */
    transform: translateX(-100%);
  }
  60.77% {
    /* 3 seconds pause for the 2nd item */
    transform: translateX(-200%);
  }
  83.85% {
    /* 3 seconds transition out of 13 seconds */
    transform: translateX(-200%);
  }
  90.77% {
    /* 3 seconds pause for the 3rd item */
    transform: translateX(-300%);
  }
  100% {
    transform: translateX(0);
  }
}

